@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  line-height: 1.15;
}

.shipping-cart {
  width: 80%;
  max-width: 1080px;
  border: 1px solid rgba(163, 163, 163, 0.5);
  margin: auto;
  margin-top: 80px;
  margin-bottom: 80px;
  height: auto;
  border-radius: 5px;
  overflow: hidden;
}

.shipping-cart__header {
  width: 100%;
  background-color: #343a40;
  padding: 10px 20px;
  color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.shipping-cart__header i {
  margin-right: 10px;
}

.shipping-cart__footer {
  width: 100%;
  background-color: #343a40;
  padding: 20px 20px;
  color: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.shipping-cart__footer span {
  font-weight: 400;
  margin: 0 10px;
  font-size: 2rem;
}

.cart__product {
  width: 100%;
  min-height: 160px;
  border: 1px solid rgba(163, 163, 163, 0.5);
  padding: 20px 30px;
  display: flex;
  flex-flow: row wrap;
  grid-row-gap: 1.8rem;
  row-gap: 1.8rem;
  align-items: center;
  justify-content: space-evenly;
  overflow: hidden;
}

.cart__product img {
  display: block;
  height: 100%;
  width: 10%;
}

.cart__product__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
  padding-left: 20px;
  width: 65%;
  font-size: 1rem;
}

.cart__product__info span {
  display: block;
  font-weight: 600;
  font-size: 1.4rem;
  margin-bottom: 10px;
}

.cart__product__controller {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: center;
  width: 20%;
  min-width: 200px;
  grid-row-gap: 0.7rem;
  row-gap: 0.7rem;
}

.cart__product__controller span {
  display: flex;
  justify-content: center;
  flex: 0 1 70px;
  align-items: center;
  font-size: 0.9rem;
}

.remove-product-from-cart-btn {
  cursor: pointer;
  flex: 0 0 37px;
  min-height: 2.18rem;
  background-color: white;
  border: 1px solid #d21313;
  color: #d21313;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  transition: all 0.2s;
}

.remove-product-from-cart-btn:hover {
  background-color: #d21313;
  color: white;
}

@media only screen and (max-width: 1000px) {
  .cart__product__info span {
    font-size: 1.1rem;
  }
  .cart__product__info {
    font-size: 0.9rem;
  }
}

@media only screen and (max-width: 760px) {
  .cart__product {
    flex-direction: column;
    grid-row-gap: 0;
    row-gap: 0;
  }
  .cart__product img {
    width: 60%;
    margin-bottom: 40px;
  }
  .cart__product__info {
    width: 100%;
    padding: 0;
    margin-bottom: 30px;
    text-align: center;
  }
  .cart__product__controller {
    width: 80%;
    margin-left: 0;
  }
}

.counter {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  flex: 1 1 90px;
}

.counter input {
  outline: none;
  border: none;
  background-color: #d3d3d3;
  width: 65%;
  height: 40px;
  text-align: center;
}

.counter__buttons__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 15%;
  min-width: 16px;
}

.counter__btn {
  width: 100%;
  height: 50%;
  cursor: pointer;
  outline: none;
  border: 1px solid rgba(163, 163, 163, 0.5);
}

.counter__btn:hover {
  background-color: rgba(163, 163, 163, 0.8);
}

.shop__wrapper {
  width: 70%;
  height: auto;
  margin: auto;
  display: grid;
  grid-template-columns: 260px 1fr;
}

@media only screen and (max-width: 840px) {
  .shop__wrapper {
    width: 90%;
  }
}

@media only screen and (max-width: 600px) {
  .shop__wrapper {
    grid-template-columns: 1fr;
  }
}

.products {
  width: 100%;
  position: relative;
  margin-bottom: 50px;
}

.products__grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 285px);
  grid-auto-rows: 570px;
  justify-content: center;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.pagination {
  margin-top: 40px;
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: white;
}

.pagination__item {
  width: 60px;
  background-color: aliceblue;
  color: black;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.125);
  cursor: pointer;
}

